#login{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
#loginButton{

    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: auto 8vh;
    border: 2px solid gray;
    border-radius: 5px;
    padding: 5px 10px;
    margin: 20px 0;
}
#loginButton img{
    height: 5vh;
    padding-right:15px;
}
#loginButton:hover{
    cursor: pointer;
    background-color: rgb(212, 208, 208);
}
.logMe{
    height: 100%;
    max-width: 60%;
}
#googleLogo{
    padding-right: 12px;
}
#triggerButton, #logout{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 90px;
    background-color: #000;
    padding-top: 2px;
    padding-right: 10px;
    border: 1.5px solid grey;
    border-radius: 3px;
    cursor: pointer;
    color: white;
}
#triggerButton:hover, #logout:hover{
    cursor: pointer;
    background-color: #585858;
}
